.layer-share {
  .layer {
    width: 360px;
    height: 320px;
    .head {
      border: 0;
      text-align: center;
    }
    .content {
      display: flex;
      padding: 30px 40px;
      height: 200px;
      .sns-wrap {
        display: inline-block;
        margin: 0 auto;
        text-align: center;
        & > a {
          display: block;
          width: 70px;
          float: left;
          margin: 0 11px;
          color: #1f1f1f;
          img {
            margin-bottom: 10px;
          }
        }
      }
      button {
        bottom: 0;
        position: absolute;
        left: 1px;
        width: 358px;
        height: 57px;
        border-width: 1px 0 0;
        font-weight: 500;
        font-size: 16px;
      }
    }
  }
}
