@charset "utf-8";

@font-face {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: 300;
  src: local('Helvetica Neue'),
  url('/_ui/font/helvetica/3469D1_3_0.eot');
  src: local('Helvetica Neue'),
  url('/_ui/font/helvetica/3469D1_3_0.eot?#iefix') format('embedded-opentype'),
  url('/_ui/font/helvetica/3469D1_3_0.woff2') format('woff2'),
  url('/_ui/font/helvetica/3469D1_3_0.woff') format('woff'),
  url('/_ui/font/helvetica/3469D1_3_0.ttf') format('truetype');
}

@font-face {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: normal;
  src: local('Helvetica Neue'),
  url('/_ui/font/helvetica/3469D1_5_0.eot');
  src: local('Helvetica Neue'),
  url('/_ui/font/helvetica/3469D1_5_0.eot?#iefix') format('embedded-opentype'),
  url('/_ui/font/helvetica/3469D1_5_0.woff2') format('woff2'),
  url('/_ui/font/helvetica/3469D1_5_0.woff') format('woff'),
  url('/_ui/font/helvetica/3469D1_5_0.ttf') format('truetype');
}

@font-face {
  font-family: 'Helvetica Neue';
  font-style: normal;
  font-weight: bold;
  src: local('Helvetica Neue'),
  url('/_ui/font/helvetica/3469D1_8_0.eot');
  src: local('Helvetica Neue'),
  url('/_ui/font/helvetica/3469D1_8_0.eot?#iefix') format('embedded-opentype'),
  url('/_ui/font/helvetica/3469D1_8_0.woff2') format('woff2'),
  url('/_ui/font/helvetica/3469D1_8_0.woff') format('woff'),
  url('/_ui/font/helvetica/3469D1_8_0.ttf') format('truetype');
}

body {
  font-family: "Helvetica Neue", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "맑은고딕", Dotum, "돋움", Gulim, "굴림", Helvetica, Arial, sans-serif;
  line-height: 1;
}
a {
  cursor: pointer;
}

.kolon-wrap {
  max-width: 1200px;
  width: 1200px;
  margin: 0 auto;
  background: #fff;
}
#kolon-content::after {
  content: '';
  display: table;
  clear: both;
}
#kolon-content .contents {
  float: left;
  width: 100%;
  max-width: 972px;
}

// header
.kolon-header {
  position: relative;
  z-index: 20;
}
header {
  .logo {
    display: inline-block;
    width: 103px;
    height: 15px;
    background: url('/_ui/img/pc/h1_logo.svg') no-repeat center;
    text-indent: -9999px;
    margin: 29px 0 28px;
  }
  .gnb {
    position: relative;
    width: 100%;
    height: 72px;
    border-bottom: 4px solid #1f1f1f;
    ul {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      li {
        display: inline-block;
        padding: 0 21px;
        line-height: 72px;
        a,
        span {
          font-weight: bold;
          font-size: 13px;
          color: #1f1f1f;
        }
      }
      .search-form {
        display: none;
      }
      .search > a {
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url('/_ui/img/pc/icn/icn-search.png');
        text-indent: -9999px;
        vertical-align: middle;
      }
      .cart {
        padding: 0;
        a {
          display: inline-block;
          width: 17px;
          height: 11px;
          background: url('/_ui/img/pc/icn/icn-cart.png');
          text-indent: -9999px;
          vertical-align: middle;
        }
        i {
          position: absolute;
          display: block;
          font-size: 10px;
          color: #fff;
          font-family: 'helvetica';
          font-weight: 700;
          width: 17px;
          height: 17px;
          border-radius: 50%;
          background: #f23d4e;
          line-height: 17px;
          text-align: center;
          text-indent: 0;
          top: 20px;
          margin-left: 10px;
        }
      }
      .language {
        a {
          &::after {
            content: '';
            display: inline-block;
            width: 7px;
            height: 5px;
            margin-top: -2px;
            margin-left: 7px;
            vertical-align: middle;
            background: url('/_ui/img/pc/icn/icn-language.png') no-repeat;
          }
        }
      }
    }
    &.search-active {
      .search {
        position: absolute;
        left: -145px;
        padding-right: 0;
        background-color: #fff;
        .search-form {
          position: relative;
          display: inline-block;
          width: 425px;
          padding-left: 15px;
          input {
            &[type="text"] {
              width: 250px;
            }
          }
          .close {
            font: 0 / 0 a;
            position: absolute;
            top: 29px;
            right: 0;
            width: 16px;
            height: 17px;
            background: url('/_ui/img/pc/icn/icn-close-layer.png') no-repeat;
          }
        }
        .search-layer {
          position: absolute;
          left: -20px;
          top: 72px;
          z-index: 1;
          background-color: #fff;
          line-height: 1;
          border: 1px solid #dedede;
          display: none;
          .inner {
            display: table;
            width: 466px;
            padding: 34px 0;
          }
          .col {
            display: table-cell;
            width: 50%;
            padding: 0 30px;
            border-left: 1px solid #f3f3f3;
            &:first-child {
              border-left: 0;
            }
            .tit {
              display: block;
              margin-bottom: 22px;
              font-size: 12px;
              color: #1f1f1f;
              font-weight: normal;
            }
            ul {
              position: static;
              &.result {
                a {
                  color: #1f1f1f;
                  span {
                    color: #898989;
                  }
                }
              }
            }
            li {
              display: block;
              margin-top: 12px;
              padding: 0;
              line-height: 1;
              &:first-child {
                margin-top: 0;
              }
              a {
                font-size: 12px;
                color: #898989;
                font-weight: normal;
                &:hover {
                  color: #1f1f1f;
                }
              }
            }
            p {
              font-size: 12px;
              color: #aaa;
            }
            .del {
              display: inline-block;
              margin-left: 5px;
              font: 0 / 0 a;
              width: 5px;
              height: 5px;
              background: url('/_ui/img/pc/icn/icn-del.png') no-repeat;
            }
          }
        }
      }
    }
    .searchbox {
      display: none;
      position: absolute;
      right: 26px;
      top: 0;
      width: 468px;
      height: auto;
      background: #fff;
      z-index: 10;
      .search-form {
        padding: 27px 0;
        .icn-search {
          display: inline-block;
          width: 18px;
          height: 18px;
          margin-right: 12px;
          background: url('/_ui/img/pc/icn/icn-search.png') no-repeat;
          background-size: 100%;
          vertical-align: middle;
        }
        input {
          &[type='text'] {
            width: 340px;
            font-size: 13px;
            color: #1f1f1f;
            border: 0;
            &::-webkit-input-placeholder {
              color: #aaa;
            }
            &::-moz-placeholder {
              color: #aaa;
            }
            &:-ms-input-placeholder {
              color: #aaa;
            }
            &:-moz-placeholder {
              color: #aaa;
            }
          }
        }
        .close {
          position: absolute;
          display: block;
          width: 15px;
          height: 15px;
          top: 27px;
          right: 16px;
          background: url('/_ui/img/pc/icn/icn-close-layer.png') no-repeat;
          background-size: 100%;
        }
      }
      .search-layer {
        overflow: hidden;
        width: 100%;
        padding: 36px 0 52px;
        border: 1px solid #dedede;
        box-sizing: border-box;
        .word {
          float: left;
          width: 50%;
          padding-left: 30px;
          font-size: 12px;
          box-sizing: border-box;
          strong {
            color: #1f1f1f;
          }
          ul {
            position: relative;
            padding-top: 26px;
            li {
              display: block;
              padding: 15px 0 0;
              line-height: 1;
              color: #898989;
              &:first-child {
                padding: 0;
              }
              a {
                font-size: 12px;
                font-weight: 400;
                color: #898989;
                &:hover {
                  color: #1f1f1f;
                }
              }
            }
          }
          &.recent {
            display: none;
            .del {
              display: inline-block;
              width: 5px;
              height: 5px;
              margin-left: 10px;
              background: url('/_ui/img/pc/icn/icn-del.png') no-repeat;
              background-size: 100%;
              text-indent: -9999px;
              vertical-align: middle;
            }
          }
          &.popular {
            border-left: 1px solid #f3f3f3;
          }
        }
      }
      .typing-layer {
        display: none;
        overflow: hidden;
        width: 100%;
        padding: 36px 30px 52px;
        border: 1px solid #dedede;
        box-sizing: border-box;
        ul {
          position: relative;
          li {
            display: block;
            padding: 15px 0 0;
            line-height: 1;
            color: #898989;
            &:first-child {
              padding: 0;
            }
            a {
              font-size: 12px;
              color: #1f1f1f;
              .typing-word {
                color: #898989;
              }
            }
          }
        }
      }
    }
  }
  .menu {
    position: relative;
    margin-top: 37px;
    &>ul>li {
      display: inline-block;
      &>a {
        font-size: 15px;
        font-weight: bold;
        color: #1f1f1f;
        &:focus,
        &:hover,
        &.active {
          border-bottom: 3px solid #1f1f1f;
        }
      }
    }
    .category {
      font-size: 0;
      letter-spacing: 0;
      word-spacing: 0;
      & > li {
        margin-right: 35px;
        & > a {
          &.active + .sub-category {
            display: block;
          }
        }
      }
      .sub-category {
        display: none;
        position: absolute;
        left: 0;
        top: 19px;
        z-index: 5;
        width: 100%;
        min-height: 440px;
        border-bottom: 3px solid #000;
        background-color: #fff;
        .fixed-bg {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          z-index: 1;
          min-height: 440px;
          background-color: #fff;
          border-bottom: 3px solid #000;
        }
        .depth-wrap {
          position: relative;
          z-index: 2;
          &::after {
            content: '';
            display: table;
            clear: both;
          }
          .depth-item {
            float: left;
            width: 120px;
            strong {
              display: block;
              margin-top: 60px;
              margin-bottom: 25px;
              a {
                font-size: 12px;
                font-weight: bold;
                color: #1f1f1f;
              }
            }
            li {
              display: block;
              margin: 0;
              a {
                display: inline-block;
                padding: 8px 0;
                font-size: 12px;
                font-weight: normal;
                color: #898989;
                &:hover,
                &:focus {
                  color: #1f1f1f;
                  border: 0;
                }
              }
            }
          }
          .banner {
            position: relative;
            float: left;
            width: 390px;
            margin-top: 90px;
            margin-left: 15px;
            &:first-child {
              margin-left: 0;
            }
            .sale {
              position: absolute;
              left: 0;
              right: 0;
              top: 65px;
              text-align: center;
              font-size: 58px;
              color: #fff;
            }
            .tit {
              margin-top: 18px;
              text-align: center;
              font-size: 13px;
              color: #1f1f1f;
            }
            .date {
              margin-top: 15px;
              text-align: center;
              font-size: 12px;
              color: #878787;
            }
          }
        }
      }
    }
    .etc {
      position: absolute;
      right: 0;
      bottom: 0;
      li {
        margin-left: 35px;
      }
    }
  }
  .brand-menu {
    display: none;
    position: absolute;
    z-index: 10;
    top: 135px;
    right: 0;
    left: 0;
    padding: 35px 0 59px;
    border-bottom: 3px solid #000;
    background: #fff;
    .fixed-bg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 1;
      min-height: 440px;
      background-color: #fff;
      border-bottom: 3px solid #000;
    }
    .fl-wrap {
      position: relative;
      z-index: 2;
      margin: 0 auto;
      width: 1200px;
      h3 {
        margin-bottom: 7px;
        color: #1f1f1f;
        font-size: 13px;
        font-weight: 700;
        line-height: 40px;
      }
      .fl-left {
        float: left;
        width: 50%;
        ul {
          width: 30%;
        }
        &::after {
          content: "";
          display: block;
          clear: both;
        }
      }
      .fl-right {
        float: right;
        width: 50%;
        ul {
          width: 25%;
        }
        &::after {
          content: "";
          display: block;
          clear: both;
        }
      }
      ul {
        float: left;
        li {
          a {
            display: block;
            color: #1f1f1f;
            font-size: 10px;
            line-height: 30px;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
  .brand-menu.on {
    display: block;
  }
}
.sub .wrap-navi {
  float: left;
  width: 154px;
  margin: 78px 0 140px;
  padding-right: 74px;
  h3 {
    margin-bottom: 48px;
    font-size: 24px;
    font-weight: 700;
    color: #1f1f1f;
    a {
      color: #1f1f1f;
    }
    span {
      display: inline-block;
      margin-top: -3px;
      margin-left: 5px;
      padding: 2px 6px;
      color: #fff;
      vertical-align: middle;
      font-size: 12px;
      border-radius: 8px;
      background-color: #1f1f1f;
    }
  }
  .sub-menu {
    min-height: 340px;
    padding-bottom: 70px;
    border-bottom: 4px solid #262626;
    font-size: 0;
    letter-spacing: 0;
    word-spacing: 0;
    dt {
      margin-top: 21px;
      a {
        font-size: 15px;
        color: #1f1f1f;
      }
    }
    dd {
      margin-top: 21px;
      a {
        font-size: 15px;
        color: #898989;
      }
    }
    dt:first-child,
    dd:first-child {
      margin-top: 0;
    }
    .bot {
      margin-top: 42px;
    }
    .before-category {
      padding-bottom: 45px;
    }
    .after-category {
      padding-top: 45px;
    }
    .content-category {
      padding-top: 45px;
    }
  }
  .sub-menu-bt {
    padding: 30px 0 24px;
    li {
      padding-bottom: 12px;
      font-size: 13px;
      color: #898989;
      a {
        display: block;
        font-size: 13px;
        color: #898989;
      }
    }
    .callcenter {
      color: #000;
      padding-bottom: 8px;
      &.sub {
        color: #898989;
        padding-bottom: 6px;
      }
    }
    .call-no {
      display: block;
      font-size: 22px;
      font-weight: 700;
      color: #1f1f1f;
      padding-bottom: 20px;
    }
    .alliance {
      padding-top: 24px;
    }
    .info-return {
      display: block;
      margin-top: 36px;
      padding: 0;
    }
  }
  .sub-menu-brand {
    position: relative;
    margin-top: 70px;
    .tit {
      font-size: 13px;
      color: #1f1f1f;
      font-weight: bold;
    }
    .reset {
      position: absolute;
      right: 0;
      top: -4px;
      padding: 4px 7px;
      font-size: 11px;
      line-height: 1;
      color: #1f1f1f;
      border: 1px solid #e5e5e5;
      background-color: #fff;
      &:hover,
      &:active {
        color: #fff;
        border: 1px solid #1f1f1f;
        background-color: #1f1f1f;
      }
    }
    ul {
      margin-top: 25px;
      margin-bottom: 40px;
      li {
        margin-top: 10px;
        font-size: 11px;
        &:first-child {
          margin-top: 0;
        }
        label {
          display: inline-block;
          width: 124px;
          padding-left: 12px;
          vertical-align: middle;
          cursor: pointer;
        }
        .checkbox {
          border-radius: 50%;
          border: 2px solid #f0f0f0;
          background: #f0f0f0;
          vertical-align: top;
          input {
            &[type='checkbox'] + i::before {
              left: -2px;
              top: -2px;
              border: 2px solid #1f1f1f;
              border-radius: 50%;
              background: #fff;
            }
          }
          &.checkbox-circle-style {
            input {
              &[type='checkbox'] + i::before {
                width: 16px;
                height: 16px;
                background: #f0f0f0;
                border: 0;
                border-radius: 20px;
                box-sizing: border-box;
              }
              &[type='checkbox']:checked {
                & + i {
                  &::before {
                    background: #fff;
                    border: 2px solid #1f1f1f;
                  }
                }
              }
            }
          }
        }
      }
    }
    .more {
      color: #1f1f1f;
      font-size: 11px;
      &::before {
        content: '';
        display: inline-block;
        width: 10px;
        height: 10px;
        margin-right: 15px;
        background: url('/_ui/img/pc/icn/icn-more-sm.png') no-repeat;
      }
    }
  }
  .sub-menu-prod {
    margin-left: -10px;
    a {
      float: left;
      width: 72px;
      height: 74px;
      margin: 0 0 10px 10px;
    }
  }
}

// footer
footer {
  position: relative;
  width: 1200px;
  max-width: 1200px;
  padding: 30px 0;
  border-top: 4px solid #1f1f1f;
  &::after {
    content: '';
    display: table;
    clear: both;
  }
  .cnt-left {
    float: left;
  }
  .f-logo {
    display: block;
    width: 103px;
    height: 15px;
    background: url('/_ui/img/pc/h1_logo.svg') no-repeat center;
    text-indent: -9999px;
  }
  .kl-industry {
    overflow: hidden;
    width: 505px;
    margin: 20px 0;
    li {
      display: block;
      font-size: 11px;
      color: #666;
      line-height: 17px;
      letter-spacing: -.2px;
    }
  }
  .logo-uplus {
    display: inline-block;
    width: 80px;
    height: 13px;
    background: url('/_ui/img/pc/icn/icn_uplus.jpg') no-repeat center;
  }
  .noti {
    margin: 4px 0 22px;
    font-size: 11px;
    color: #898989;
    a {
      color: #1f1f1f;
      text-decoration: underline;
    }
  }
  .copyright {
    font-family: 'helvetica';
    font-size: 10px;
    color: #1f1f1f;
  }
  .cnt-right {
    float: right;
    min-width: 350px;
    h4 {
      margin-bottom: 15px;
      font-size: 11px;
      color: #1f1f1f;
      font-weight: bold;
    }
    div {
      display: inline-block;
      vertical-align: top;
    }
    ul li a {
      font-size: 11px;
      color: #666;
      line-height: 17px;
    }
    .cstm {
      margin-top: 26px;
      margin-left: 65px;
    }
    .callcenter {
      margin: 28px 0 0 55px;
      li {
        font-size: 11px;
        color: #666;
        &:last-child {
          margin-top: 6px;
        }
      }
      .call-no {
        padding: 0 0 10px;
        font-family: 'helvetica';
        font-size: 20px;
        font-weight: bold;
        color: #1f1f1f;
      }
    }
    .cmnty {
      display: block;
      margin-top: 50px;
      font-size: 10px;
      font-weight: bold;
      color: #1f1f1f;
      &::before {
        content: '';
        display: inline-block;
        margin-right: 5px;
        background: url('/_ui/img/pc/icn/icn-footer.png') no-repeat;
      }
    }
    .sms {
      margin-top: 57px;
      &::before {
        width: 10px;
        height: 14px;
        background-position: 0 0;
      }
    }
    .facebook::before {
      width: 8px;
      height: 14px;
      background-position: -11px 0;
    }
  }
  .box-sns {
    position: absolute;
    right: 5px;
    bottom: 30px;
    .sns {
      font-family: 'helvetica';
      font-size: 10px;
      color: #aaa;
    }
    i {
      display: inline-block;
      width: 1px;
      height: 8px;
      margin: 0 15px;
      background: #e1e1e1;
    }
  }
  .btn-top {
    position: absolute;
    right: -40px;
    top: -20px;
    bottom: 0;
    width: 25px;
    height: 20px;
    font: 0 / 0 a;
    background: url('/_ui/img/pc/icn/icn-top.png') no-repeat;
    transition: bottom ease .3s;
    &.active {
      position: fixed;
      left: 50%;
      margin-left: 615px;
      bottom: 230px;
      top: auto;
    }
  }
  .e-kolon {
    margin: 0;
  }
}

@media (min-width: 1200px) {
  body {
    overflow-x: hidden;
  }
}
