@charset "utf-8";
@import "../../includes/util";

// top banner
.desktop-top-line-banner {
  .top-line-banner {
    display: block;
    width: 100%;
    overflow: hidden;
  }
  img {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
}

// tab
.tab {
  overflow: hidden;
  position: relative;
  width: 100%;
  padding-bottom: 106px;
  li {
    float: left;
    border-top: 1px solid #dedede;
    border-right: 1px solid #dedede;
    border-bottom: 1px solid #dedede;
    text-align: center;
    box-sizing: border-box;
    &:first-child {
      border-left: 1px solid #dedede;
    }
    a {
      font-size: 14px;
      color: #aaa;
      display: inline-block;
      width: 100%;
      padding: 14px 0 18px;
    }
    &.on {
      background-color: #1f1f1f;
      a {
        color: #fff;
      }
      .smenu {
        display: block;
        li {
          &.on {
            background-color: transparent;
            a {
              color: #1f1f1f;
            }
          }
        }
      }
    }
  }
  .smenu {
    position: absolute;
    bottom: 46px;
    left: 10px;
    display: none;
    li {
      display: inline-block;
      border: 0;
      a {
        color: #aaa;
        padding: 0;
        &::before {
          content: '';
          display: inline-block;
          width: 1px;
          height: 13px;
          margin: 0 18px;
          background-color: #eaeaea;
        }
      }
      &:first-child {
        a::before {
          display: none;
        }
      }
    }
  }
}
.tab.wrap-smenu {
  padding-bottom: 113px;
}
.tab.list2 > li {
  width: 50%;
}
.tab.list3 > li {
  width: 33.334%;
}
.tab.list4 > li {
  width: 25%;
}
.tab.list6 > li {
  width: 16.66%;
}

// qna form
.qna-form {
  width: 100%;
  border-top: 4px solid #1f1f1f;
  dl {
    border-bottom: 1px solid #f1f1f1;
    dt {
      @include long-text;
      position: relative;
      margin: 0 30px;
      padding: 23px 132px 23px 34px;
      font-size: 14px;
      color: #898989;
      background: url('/_ui/img/pc/icn/icn-qustion.png') no-repeat center left;
      cursor: pointer;
      line-height: 22px;
      &::after {
        position: absolute;
        content: '';
        display: inline-block;
        width: 16px;
        height: 9px;
        right: 0;
        top: 50%;
        background: url('/_ui/img/pc/icn/icn-arrow.png');
        background-position: 0 -9px;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        transform: translateX(-50%);
      }
    }
    dd {
      @include long-text;
      display: none;
      padding: 25px 220px 34px 64px;
      line-height: 24px;
      font-size: 14px;
      color: #898989;
      background: #f8f8f8 url('/_ui/img/pc/icn/icn-answer.png') no-repeat 30px 40%;
    }
  }
  .on dt {
    color: #1f1f1f;
    background: url('/_ui/img/pc/icn/icn-qustion-atv.png') no-repeat center left;
    &::after {
      background-position: 0 0;
    }
  }
}

// more
.list-more {
  font: 0 / 0 a;
  display: block;
  width: 30px;
  height: 30px;
  margin: 40px auto 0;
  background: url('/_ui/img/pc/icn/icn-more.png') no-repeat;
}

// button
.buttons.end-row {
  text-align: center;
  margin-top: 60px;
  padding-top: 60px;
  border-top: 2px solid #000;
  .btn {
    margin: 0 10px;
  }
}
.btn {
  color: #fff;
  font-size: 14px;
  line-height: 40px;
  vertical-align: middle;
  display: inline-block;
  box-sizing: border-box;
  height: 40px;
  padding: 0 25px;
  background-color: #000;
}
.btn-line {
  color: #000;
  border: 1px solid #ccc;
  background-color: #fff;
}
.btn-big {
  min-width: 120px;
  font-size: 16px;
  line-height: 46px;
  height: 46px;
}
.btn-medium {
  padding: 0 20px;
  line-height: 36px;
  height: 36px;
}
.btn-small {
  font-size: 13px;
  line-height: 30px;
  height: 30px;
}

// form
.select {
  display: inline-block;
  select {
    color: #898989;
    font-size: 14px;
    vertical-align: top;
    display: inline-block;
    width: 100%;
    height: 38px;
    &:focus {
      color: #898989;
      border-color: #1f1f1f;
    }
  }
}

// 날짜 검색
.search-date {
  margin-bottom: 50px;
  .tab {
    float: left;
    width: auto;
    padding-bottom: 0;
    li {
      width: 85px;
      a {
        color: #000;
        font-size: 13px;
        height: 26px;
        padding: 12px 0 0;
      }
    }
    .on {
      background-color: #898989;
      a {
        color: #fff;
      }
    }
  }
  .date {
    float: right;
    input,
    button {
      float: left;
      height: 40px;
      box-sizing: border-box;
    }
    input {
      width: 160px;
      padding: 0 33px 0 12px;
      border: 1px solid #dedede;
      background: url('/_ui/img/pc/icn/icn-calrendar.png') no-repeat 135px 50%;
    }
    .btn {
      margin-left: 10px;
    }
    .split {
      font-size: 14px;
      text-align: center;
      line-height: 40px;
      float: left;
      width: 17px;
    }
  }
  &::after {
    content: '';
    display: block;
    clear: both;
  }
}

.paging {
  text-align: center;
  a,
  .on {
    display: inline-block;
    width: 28px;
    height: 28px;
    text-align: center;
    font-size: 12px;
    line-height: 2.2;
  }
  .on {
    font-weight: bold;
    background-color: #f2f2f2;
  }
  .next,
  .prev {
    font: 0 / 0 a;
    vertical-align: middle;
    border: 0;
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
  .next {
    background-image: url('/_ui/img/pc/icn/icn-paging-next.png');
  }
  .prev {
    background-image: url('/_ui/img/pc/icn/icn-paging-prev.png');
  }
}

