.square-banner {
  position: relative;
  background: #f0f0f0;
  .bg-image {
    width: 100%;
    background-size: cover;
    background-position: center center;
  }
  .sticky {
    position: absolute;
    left: 18px;
    top: 0;
    width: 52px;
    height: 52px;
    text-align: center;
    font-size: 12px;
    color: #fff;
    background-color: #1f1f1f;
    span {
      display: inline-block;
    }
    .type1 {
      margin-top: 20px;
    }
    .type2 {
      margin-top: 14px;
      line-height: 1.3;
    }
  }
  .txt-box {
    position: absolute;
    bottom: 23px;
    left: 20px;
    color: #fff;
    span {
      display: block;
    }
    &.white {
      .brand>span {
        background: #fff;
        color: #0f0f0f;
      }
      .tit,
      .txt {
        color: #fff;
      }
    }

    .brand {
      font-size: 12px;
      font-weight: bold;
      color: #fff;
      margin-bottom: 19px;
      height: 20px;
      line-height: 16px;
    }
    .brand>span {
      float: left;
      display: block;
      margin-right: 2px;
      padding: 0 6px;
      height: 15px;
      background: #000;
      font-weight: inherit;
      color: #fff;
    }
    .tit {
      font-size: 26px;
      font-weight: 700;
      line-height: 32px;
      color: #000;
      text-align: left;
      margin-bottom: 12px;
    }
    .txt {
      font-size: 15px;
      color: #000;
      letter-spacing: -.3px;
    }
  }
}

// banner list
.thumb-wrap {
  &.with-lnb {
    .thumb-list .item {
      width: 310px;
    }
  }

  .tit {
    margin-bottom: 40px;
    font-weight: 300;
    text-align: center;
    font-size: 26px;
    color: #0f0f10;
  }
  .thumb-list {
    margin-left: -15px;
    &::after {
      content: '';
      display: table;
      clear: both;
    }
    .item {
      position: relative;
      float: left;
      width: 390px;
      margin-left: 15px;
      margin-bottom: 15px;
    }
    &.slide-list {
      position: relative;
      margin: 0;
      .item {
        margin-left: 0;
        margin-bottom: 0;
      }
    }
  }
}

// slide
.swiper-wrapper {
  max-width: 972px;
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  -ms-transition-property: -ms-transform;
  transition-property: transform;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  .swiper-slide {
    background: #fff;
    webkit-flex-shrink: 0;
    -ms-flex: 0 0 auto;
    flex-shrink:  0;
    height:  100%;
    position:  relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    &.square-banner {
      background: #f0f0f0;
    }
  }
  .swiper-slide>a {
    display: block;
    width: 100%;
  }
}
.swiper-container-horizontal {
  position: relative;
  &:hover .swiper-controls {
    display: block;
  }
}
.swiper-controls {
  display: none;
  .swiper-button-prev,
  .swiper-button-next {
    position: absolute;
    z-index: 2;
    top: 50%;
    display: inline-block;
    width: 45px;
    height: 45px;
    margin-top: -23px;
    font: 0 / 0 a;
  }
  .swiper-button-prev {
    left: 0;
    background: url('/_ui/img/pc/icn/icn-swipe-prev.gif') no-repeat;
  }
  .swiper-button-next {
    right: 0;
    background: url('/_ui/img/pc/icn/icn-swipe-next.gif') no-repeat;
  }
}
.swiper-pagination {
  width: 100%;
  height: 5px;
  left: 0;
  bottom: 0;
  background: transparent;
  position: absolute;
  text-align: center;
  -webkit-transition: .3s;
  -moz-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  z-index: 10;
  opacity: .8;
  .swiper-pagination-progressbar {
    background: #1f1f1f;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
    -webkit-transform-origin: left top;
    -moz-transform-origin: left top;
    -ms-transform-origin: left top;
    -o-transform-origin: left top;
    transform-origin: left top;
  }
}

