.spinner {
  position: relative;
  display: inline-block;
  width: 40px;
  padding: 0 25px;
  input {
    font-size: 13px;
    text-align: center;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    border-bottom: 1px solid #dedede;
  }
  button {
    font: 0 / 0 a;
    position: absolute;
    top: 0;
    height: 30px;
    width: 25px;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 7px;
      height: 1px;
      margin: -.5px 0 0 -3.5px;
      background-color: #1f1f1f;
    }
  }
  .minus {
    left: 0;
  }
  .plus {
    right: 0;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 1px;
      height: 7px;
      margin: -3.5px 0 0 -.5px;
      background-color: #1f1f1f;
    }
  }
}
.category-top-menu {
  position: relative;
  & > h3 {
    font-size: 22px;
    color: #1f1f1f;
    .cnt {
      display: inline-block;
      margin-top: -3px;
